___CSS_LOADER_EXPORT___.push([module.id, `/* 1. Use a more-intuitive box-sizing model */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

body {
  /* 3. Add accessible line-height */
  line-height: 1.5;
  /* 4. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 5. Improve media defaults */
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

/* 6. Inherit fonts for form controls */
input,
button,
textarea,
select {
  font: inherit;
}

/* 7. Avoid text overflows */
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

/* 8. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

/*
  9. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}

/* -------------------------------------------- */

body {
  display: flex;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

/* ------------ side bar ------------*/

#side-bar {
  flex: 1;
  height: 100vh;
  border-top: none;
  background-color: #ededed;

  padding-left: 30px;
  padding-top: 60px;
}

#side-bar h1 {
  margin-bottom: 40px;
}

#side-bar h3 {
  margin-bottom: 12px;
}

#side-bar input {
  margin-bottom: 32px;
  width: 150px;
  margin-right: 8px;
}

/* Project Styles (Sidebar) */
#projects > div {
  /* Targeting the direct children (project divs) */
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  margin-bottom: 16px;
  cursor: pointer;
}

#projects > div > button {
  /* Targeting the remove button inside project divs */
  border: none;
  background-color: white;
  cursor: pointer;
  font-size: 16px;
}

#projects img {
  max-width: 16px;
}

/* ------------ tasks display ------------*/
#main {
  flex: 3;
  height: 100vh;
  border-top: none;
  padding-left: 30px;
  padding-top: 60px;
}

#tasks h2 {
  margin-bottom: 30px;
}

#tasks input {
  margin-right: 16px;
  width: 16px;
}

#tasks > div {
  width: 700px;
}

/* tasks */
#tasks > div {
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgb(201, 201, 201);
  display: flex;
  justify-content: start;
  cursor: pointer;
}

#tasks > div > div {
  width: 500px;
}

#tasks button {
  background-color: white;
  border: none;
}

#tasks img {
  max-width: 20px;
  cursor: pointer;
}

#open-form-button {
  margin-top: 32px;
}

#finished-tasks-div {
  margin-top: 32px;
}

/* form */
form {
  padding-left: 0;
  margin-top: 32px;
  margin-left: 12px;
}

#taskTitle,
#taskDescription {
  width: 250px;
}
#taskTitle {
  margin-right: 12px;
}

#taskDueDate {
  margin-right: 55px;
}

#taskPriority {
  margin-right: 12px;
}

form > div {
  margin-top: 16px;
}

form button {
  padding-left: 14px;
  padding-right: 14px;
}

/* modal popup */
#task-modal-popup {
  position: fixed;
  top: 100px;
  left: 500px;
  width: 400px;
  padding: 30px;
  background-color: white;

  z-index: 1;

  box-shadow: 0px 0px 20px rgb(222, 222, 222);

  display: none;
}

#task-modal-popup > h2 {
  margin-bottom: 20px;
}

#task-modal-popup #modal-title,
#task-modal-popup #modal-description {
  margin-bottom: 12px;
}

#task-modal-popup #modal-description {
  width: 100%;
}

#due-date-div {
  margin-bottom: 12px;
}

#close-modal {
  position: absolute;
  right: 30px;
  top: 20px;
  font-size: 24px;
  cursor: pointer;
  padding: 4px;
}

#update-modal {
  padding: 8px;
  text-align: center;
  border-radius: 3px;
  margin-top: 40px;
  background-color: rgb(235, 235, 235);
  cursor: pointer;
  transition: 0.1s;
}

#update-modal:hover {
  background-color: rgb(210, 210, 210);
  transition: 0.1s;
}
